import { Component, OnInit } from '@angular/core';
import {
  ControlValueAccessor,
  NG_VALUE_ACCESSOR,
  FormBuilder,
  FormGroup,
  Validators,
  FormControl,
  NG_VALIDATORS,
} from "@angular/forms";
import { ApiService } from "app/shared/services/api.service";

@Component({
  selector: 'app-branch-login',
  templateUrl: './branch-login.component.html',
  styleUrls: ['./branch-login.component.scss']
})
export class BranchLoginComponent implements OnInit {
  branchLogin: FormGroup;
  optionType: any;
  radioType: any;
  kioskCode: any[]=[];
  systemCode: any;

  constructor(
    private ApiService: ApiService,
  ) { }

  ngOnInit(): void {
    this.fetchKioskCode();
  }
  fetchKioskCode(){
    this.ApiService.fetchKioskSystemDetails().subscribe((data) => {      
      for(let i=0; i<data.length;i++){
        console.log(data[i].kioskSystemCode);
        
          this.kioskCode.push(data[i].kioskSystemCode);
          console.log('auth',i)
       
      }
      
    });   
  }

  onChangeOption(event) {
    console.log(event);
    this.optionType = event.value;
  }

  onChangeValue(event){
    this.radioType = event.value;
  }

}
